@font-face {
  font-family: "SegoeUI";
  src: url("../fonts/SegoeUI/SegoeUI.woff2") format("woff2"),
    url("../fonts/SegoeUI/SegoeUI.woff") format("woff"),
    url("../fonts/SegoeUI/SegoeUI.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "SegoeUI";
  src: url("../fonts/SegoeUI/SegoeUI-Italic.woff") format("woff")
    url("../fonts/SegoeUI/SegoeUI-Italic.ttf") format("truetype");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "SegoeUI";
  src: url("../fonts/SegoeUI/SegoeUI-Bold.woff2") format("woff2"),
    url("../fonts/SegoeUI/SegoeUI-Bold.woff") format("woff"),
    url("../fonts/SegoeUI/SegoeUI-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "SegoeUI";
  src: url("../fonts/SegoeUI/SegoeUI-BoldItalic.woff") format("woff"),
    url("../fonts/SegoeUI/SegoeUI-BoldItalic.ttf") format("truetype");
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "SegoeUI";
  src: url("../fonts/SegoeUI/SegoeUI-Light.woff2") format("woff2"),
    url("../fonts/SegoeUI/SegoeUI-Light.woff") format("woff"),
    url("../fonts/SegoeUI/SegoeUI-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "SegoeUI";
  src: url("../fonts/SegoeUI/SegoeUI-Semibold.woff2") format("woff2"),
    url("../fonts/SegoeUI/SegoeUI-SemiBold.woff") format("woff"),
    url("../fonts/SegoeUI/SegoeUI-SemiBold.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
